.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;

  @include vp-1199 {
    padding: 0 45px;
  }

  @include vp-767 {
    min-width: 320px;
    padding: 0 15px;
  }

  &--wrapper {
    display: flex;
    flex-direction: row;

    @include vp-1199 {
      flex-direction: column;
    }
  }
}
