.advantages {
  background-color: $color-ghost-white;
}

.advantages__container {
  padding-top: 118px;
  padding-bottom: 115px;
  text-align: center;

  @include vp-1199 {
    padding-top: 99px;
    padding-bottom: 93px;
  }

  @include vp-767 {
    padding-top: 69px;
    padding-bottom: 62px;
  }
}

.advantages h2 {
  margin-bottom: 59px;

  @include vp-1199 {
    margin-bottom: 42px;
  }
}

.advantages__list {
  margin: 0 60px;

  @include vp-1199 {
    margin: 0;
  }
}
